import React from 'react'
import { graphql } from 'gatsby'

import styled from 'styled-components'

import Layout from 'components/Layout'

interface MetaProps {
  location: {
    search: string
  }
  data: {
    site: {
      siteMetadata: {
        title: string
        description: string
        siteUrl: string
      }
    }
  }
}

export default function Home({
  location: { search },
  data: {
    site: {
      siteMetadata: { title, description, siteUrl },
    },
  },
}: MetaProps) {
  return (
    <Layout title={title} description={description} url={siteUrl}>
      <PageTitle>I'm Wonjong Jang,</PageTitle>
      <PageTitle>a Frontend Developer.</PageTitle>
      <PageDescript>프론트엔드 개발자 장원종입니다.</PageDescript>
    </Layout>
  )
}

export const getPostList = graphql`
  query getPostList {
    site {
      siteMetadata {
        title
        description
        siteUrl
      }
    }
  }
`

export const Section = styled.div`
  margin-top: 48px;

  @media screen and (min-width: 1024px) {
    margin-top: 72px;
  }
`

export const PageDescript = styled.p`
  margin-top: 4px;
  font-size: 22px;
  font-weight: 400;
  color: ${props => props.theme.subTextColor};

  @media screen and (min-width: 1024px) {
    font-size: 28px;
  }
`

export const PageTitle = styled.p`
  font-size: 30px;
  font-weight: 700;

  @media screen and (min-width: 1024px) {
    font-size: 36px;
  }
`
