import React, { ReactNode } from 'react'
import { Link } from 'gatsby'

import styled from 'styled-components'

export interface CategoryListProps {
  selectedCategory: string
  categoryList: {
    [key: string]: number
  }
}

interface CategoryItemProps {
  active: boolean
}

interface GatsbyLinkProps {
  children: ReactNode
  className?: string
  to: string
  active: boolean
}

export default function CategoryList({
  selectedCategory,
  categoryList,
}: CategoryListProps) {
  return (
    <CategoryListWrapper>
      {Object.entries(categoryList).map(([name, count]) => (
        <CategoryItem
          to={`/tech-blog/?category=${name}`}
          active={name === selectedCategory}
          key={name}
        >
          {name}
        </CategoryItem>
      ))}
    </CategoryListWrapper>
  )
}

const CategoryItem = styled(({ active, ...props }: GatsbyLinkProps) => (
  <Link {...props} />
))<CategoryItemProps>`
  margin-right: 10px;
  height: 37px;
  padding: 0 17px;
  font-size: 14px;
  line-height: 37px;
  border-radius: 30px;
  letter-spacing: -0.5px;
  cursor: pointer;
  &:hover {
    color: ${props => props.theme.bgColor};
    background: ${props => props.theme.selectBoxColor};
  }
  color: ${props =>
    props.active ? props.theme.bgColor : props.theme.textColor};
  background: ${props =>
    props.active ? props.theme.selectBoxColor : props.theme.tabColor};

  @media screen and (min-width: 1024px) {
    height: 44px;
    padding: 0 20px;
    font-size: 16px;
    line-height: 43px;
  }
`

const CategoryListWrapper = styled.div`
  display: flex;
`
