import React, { useMemo } from 'react'
import { graphql } from 'gatsby'
import queryString, { ParsedQuery } from 'query-string'

import { PageDescript, PageTitle, Section } from 'pages'

import Layout from 'components/Layout'
import CategoryList, { CategoryListProps } from 'components/CategoryList'
import PostList, { PostListItemType } from 'components/PostList'

interface TechBlogProps {
  location: {
    pathname: string
    search: string
    href: string
  }
  data: {
    allMarkdownRemark: {
      edges: PostListItemType[]
    }
  }
}

export default function TechBlog({
  location: { pathname, search, href },
  data: {
    allMarkdownRemark: { edges },
  },
}: TechBlogProps) {
  const parsed: ParsedQuery<string> = queryString.parse(search)
  const selectedCategory: string =
    typeof parsed.category !== 'string' || !parsed.category
      ? 'All'
      : parsed.category

  // tech-blog에 해당하는 게시물
  const pageEdges = edges.filter(
    edge => edge.node.frontmatter.page === 'tech-blog',
  )

  // tech-blog에 해당하는 게시물 중 카테고리 별 개수
  const categoryList = useMemo(
    () =>
      pageEdges.reduce(
        (
          list: CategoryListProps['categoryList'],
          {
            node: {
              frontmatter: { categories },
            },
          }: PostListItemType,
        ) => {
          categories.forEach(category => {
            if (list[category] === undefined) list[category] = 1
            else list[category]++
          })

          list['All']++

          return list
        },
        { All: 0 },
      ),
    [],
  )

  return (
    <Layout title="TechBlog" description="개발하면서 남긴 기록" url={href}>
      <div>
        <PageTitle>Tech Blog</PageTitle>
        <PageDescript>개발하면서 남긴 기록</PageDescript>
      </div>
      <Section>
        {/* 카테고리 */}
        <CategoryList
          selectedCategory={selectedCategory}
          categoryList={categoryList}
        />
        {/* 게시물 목록 */}
        <PostList selectedCategory={selectedCategory} posts={pageEdges} />
      </Section>
    </Layout>
  )
}

export const getPostList = graphql`
  query getPostList {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date, frontmatter___title] }
    ) {
      edges {
        node {
          id
          fields {
            slug
          }
          frontmatter {
            page
            categories
            date(formatString: "YYYY.MM.DD")
            title
            summary
          }
        }
      }
    }
  }
`
